import React , {useEffect , useState} from 'react'
import { Form, Input, Button } from 'antd'
import { PhoneOutlined } from "@ant-design/icons"; //  


export default function Login() {

const [phone , setphone] = useState("");
const [password , setpassword] = useState("");
const[isDisabled , setisDisabled] = useState(true);


useEffect(()=>{
if(phone.trim() !== "" && password.trim()!== ""){
setisDisabled(false)
}else{
setisDisabled(true)
}
},[password , phone])


    return (
        <div className='container'>
            <Form className='my-form' layout="vertical" >

                <img src={`${process.env.PUBLIC_URL}/ax.png`} alt="logo" className='img' />

                <p className='p-first' ><b>   LumiPay</b></p>
                <p style={{ color: 'gray' }} ><b>  </b></p>

                <Form.Item label="* " name='phone'>
                    <Input value={phone} onChange={(e)=>setphone(e.target.value)} placeholder='09xxxxxxxxx' prefix={<PhoneOutlined style={{ color: "gray" }} />} style={{ width: "100%" }} />
                    <p className='text'>        </p>
                </Form.Item>

                <Form.Item label="* " name='password'>
                    <Input.Password value={password} onChange={(e)=>setpassword(e.target.value)} type='password' placeholder=' 6 ' style={{ width: "100%" }} />
                </Form.Item>

                <Form.Item >
                    <Button disabled={isDisabled} type="primary" block className='big-btn'>
                          
                    </Button>
                    <p style={{ marginTop: '5%', color: 'gray' }}>      </p>
                </Form.Item>

            </Form>
        </div>

    )
}